import {useEffect, lazy, Suspense, useState, useContext} from 'react';

import {ic_check} from 'react-icons-kit/md/ic_check'
import {ic_close} from 'react-icons-kit/md/ic_close'
import logo1 from './image/logo_gray.png'

import Flickity from 'react-flickity-component';
import 'flickity-as-nav-for';

function Images({images}){
// console.log(images);
    
    const flickityOptions = {
        pageDots: true,
        wrapAround: true,
        prevNextButtons:true,
        pageDots:true
    }
    const flickityOptions_sub = {
        asNavFor: ".carousel1_single",
        cellAlign: 'left',
        contain: true,
        prevNextButtons:false,
        pageDots: false
    }
    const flickityOptions_single = {
        cellAlign: 'left',
        wrapAround: true,
        prevNextButtons:true,
        pageDots: false,
        contain: true

    }


    return(
        <div className='w-full mt-4'>
                {images == null 
                ?
                <div className='imgg flex justify-center items-center bg-gray-200'>
                <img src={logo1} className="img1" />
                </div>
                :
                <Flickity
                className={'carousel1_single'} // default ''
                elementType={'div'} // default 'div'
                options={flickityOptions} // takes flickity options {}
                disableImagesLoaded={false} // default false
                reloadOnUpdate // default false
                static // default false
                >

                {images?.map((img)=>(
                    <div className="carousel1_single-cell" 
                    // style={{ backgroundImage: `url(https://gamysh.com/${img?.destination}-big.webp)` }}
                    >
                        <img src={`https://gamysh.com/${img?.destination}-large.webp`} />
                    </div>
                ))}

                </Flickity>
                

                }

                {images == null 
                        ?
                        null
                        :
                    <Flickity
                    className={'carousel-nav'} // default ''
                    elementType={'div'} // default 'div'
                    options={flickityOptions_sub} // takes flickity options {}
                    disableImagesLoaded={false} // default false
                    reloadOnUpdate // default false
                    static // default false
                    >
                        {images?.map((img1)=>(
                                <div className="carousel1_sub-cell mx-4 mt-5"
                                >
                                    <img src={`https://gamysh.com/${img1?.destination}-big.webp`} className="img_sub" />
                                </div>
                            ))}
                            
                    </Flickity>
                    }
        
        </div>
    )
}

export default Images;

