import {useEffect, lazy, Suspense, useState, useContext} from 'react';
import { Route, Switch, useRouteMatch, useHistory, Redirect, Link, useLocation } from 'react-router-dom';
import image from './image/gamysh.jpg';
import image1 from './image/hotel.jpg'
import image2 from './image/hotel1.jpg'
import image3 from './image/hotel2.jpeg'
import { Icon } from 'react-icons-kit'
import logo1 from './image/logo_gray.png'

import {smallDown} from 'react-icons-kit/entypo/smallDown';
import {heartO} from 'react-icons-kit/fa/heartO'
import {mapMarker} from 'react-icons-kit/fa/mapMarker'
import {ic_room} from 'react-icons-kit/md/ic_room'
import {ic_check} from 'react-icons-kit/md/ic_check'
import {ic_close} from 'react-icons-kit/md/ic_close'
import {ic_swap_vert} from 'react-icons-kit/md/ic_swap_vert'
import {map} from 'react-icons-kit/fa/map'
import {ic_tune} from 'react-icons-kit/md/ic_tune'
import {ic_delete} from 'react-icons-kit/md/ic_delete'
import {ic_visibility_twotone} from 'react-icons-kit/md/ic_visibility_twotone'
import Flickity from 'react-flickity-component';
import 'flickity-as-nav-for';
import {useDispatch, useSelector} from 'react-redux';
import { post, fetchData } from '../application/middlewares/index';
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet';
import Lang1 from '../pages/Language/Lang1';
import { LangContext } from "../pages/Language/LangContext";
import Loading from '../components/Loading';
import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';
import { useForm, Controller} from "react-hook-form";
import {getToken} from '../application/selectors/auth';
import toast from 'react-hot-toast';
import Button from "@material-tailwind/react/Button";
import Images from './Images';







const Navbar = lazy(() => import('./Navbar'))
const MapSingle = lazy(() => import('./MapSingle'))
const Footer = lazy(() => import('./Footer'))


function Single(){

     const { lang, dispatchLang } = useContext(LangContext);
    const dispatch = useDispatch();
    const token = useSelector(getToken);
  const { register ,watch, handleSubmit, formState: { errors }, setError, setValue, getValues} = useForm({
    resolver: yupResolver(schema(lang)),
    // defaultValues:{...update}
});

    let location = useLocation();
    const [loading, setLoading] = useState(true);
    const [arz, setArz] = useState(false);
    const [image_modal, setImage_modal] = useState(false);

    // console.log(location.pathname);
    let single = []; 
        var s=location.pathname;
        single = s.split('/');
    var single_id = parseInt(single[2]);
    // console.log(single)
    const [products, setProducts] = useState();
    let dil;
    if (lang=='ru') 
    {dil='ru'} 
    else 
    {dil='tm'}

    useEffect(() => {
        dispatch(fetchData({
            url:`${dil}/real-estate/${single_id}`,
            action:(response)=>{
                if(response.success){
                    // console.log("get_single ",response)
                    // console.log(response.data.rows[0].real_estates_all[0])
                    setProducts(response?.data?.rows);
                    setLoading(false);
                    // console.log(products)
                } else {
                    console.log("error_data ",response);
                    }
                }
            }))
    }, [dil]);
    const onSubmit = (data) => {
        // console.log(data,"----message_complaint");
        if (token){
        dispatch(post({
            url:`user/${dil}/make-complaint/${single_id}`,
            token: token,
            data: data,
            action:(response)=>{
                if(response.success){
                    // console.log("complaint_response ",response)
                    toast.success(Lang1[lang].success_description);
                    setValue("message", '')
                } else {
                    // console.log("error_data ",response);
                    toast.error(Lang1[lang].errors);
                    // if (response?.message?.data){
                    //     let keys = Object?.keys(response?.message?.data?.error);
                    //     // console.log(keys,"------keys");
                    //     keys?.map(item => {
                    //         setError(item, {type:'manual', message:`${Lang1[lang].required}`})
                    //     })
                    //  }
                    }
                }
            }))
        } else {
            setArz(false);
            toast.error(Lang1[lang].error_description)
        }
    }


    const [sort, setSort] = useState("false");
    const ToggleSort = () => {
        setSort(!sort); 
       };
       const [filter, setFilter] = useState("false");
    const ToggleFilter = () => {
        setFilter(!filter); 
       };

       const flickityOptions = {
        pageDots: true,
        wrapAround: true,
        prevNextButtons:true,
        pageDots:true
    }
    const flickityOptions_sub = {
        asNavFor: ".carousel1",
        cellAlign: 'left',
        contain: true,
        prevNextButtons:false,
        pageDots: false
    }
    const flickityOptions_single = {
        cellAlign: 'left',
        wrapAround: true,
        prevNextButtons:true,
        pageDots: false,
        contain: true

    }

    // console.log(products?.position[0]?.x)

    // console.log(products?.position[0]?.y)
       
    return(
        <main>
            <Navbar />

            <div className="main py-6 border-t bg-gray-100">
                <div className="main1">
                <p className="text-gray-600 text-sm">{Lang1[lang].text}</p>
                {/* <p className="text-blue-600  flex items-center mt-4">
                    <p className="text-gray-600 mr-3">сегодня, 13:21</p> 
                <Icon size={18} icon={ic_visibility_twotone} className="text-blue-600 mx-3"/>
                446 просмотров</p> */}
                </div>
            </div>
            <div className="main py-3 sm:py-6 bg-gray-100 single_padding">
                <div className="main1 bg-white p-4 border rounded-sm">
                    {
                        loading ?
                        <div className='h-screen w-full'>
                        <Loading type="table"/>
                        </div>
                        :
                        <>
                    <h1 className="text-xl sm:text-2xl font-medium">{products?.real_estate_name}</h1>
                    <h1 className="text-base sm:text-xl font-medium mt-2 sm:mt-3">{products?.location}</h1>
                    <p className="text-sm text-gray-500 my-1 sm:my-3">
                    <a href={`tel:+993${products.phone}`}>+993 {products.phone}</a>
                    </p>
                    <h1 className="text-base sm:text-xl font-medium mb-5">
                        {/* {products?.price} */}
                        {products?.price.slice(0, products?.price.length%3)}{products?.price.length%3 ? ` ` : ``}{(products?.price.slice(products?.price.length%3, products?.price.length).match(/.{1,3}/g).join(' '))} 
                     <span className='text-xs'>TMT</span></h1>
                    <div className="w-full flex flex-col sm:flex-row justify-between">
                    <div className="slider2 relative">
                        {products?.images == null 
                                ?
                                <div className='imgg flex justify-center items-center bg-gray-200'>
                                <img src={logo1} className="img1" />
                                </div>
                                :
                                <>
                                
                                {
                                    image_modal ?
                                    <div className='image_modal' style={{backgroundColor: 'black'}}>
                                        <div className="filter_header border-b flex items-center justify-between px-4">
                                             <h1 className="text-xl text-white">{products?.real_estate_name}</h1>
                                             <Icon size={22} icon={ic_close} 
                                            onClick={()=>{
                                            setImage_modal(false)
                                            }} 
                                        className="text-white text-medium cursor-pointer"/>
                                        </div>
                                        <Images images={products?.images} />
                                    </div>
                                    :
                                    null
                                }

                            <Flickity
                            className={'carousel1'} // default ''
                            elementType={'div'} // default 'div'
                            options={flickityOptions} // takes flickity options {}
                            disableImagesLoaded={false} // default false
                            reloadOnUpdate // default false
                            static // default false
                            >
                                
                                {products?.images?.map((img)=>(
                                        <div className="carousel1-cell single_img" 
                                        style={{ backgroundImage: `url(https://gamysh.com/${img?.destination}-big.webp)` }}
                                        onClick={()=>{
                                            setImage_modal(true)
                                         }}
                                        >
                                            {/* <img src={`https://gamysh.com/${img?.destination}-big.webp`} className="img" /> */}
                                        </div>
                                    ))}
                                
                            </Flickity>
                            </>

                            }
                            {products?.images == null 
                                ?
                                null
                                :
                            <Flickity
                            className={'carousel-nav'} // default ''
                            elementType={'div'} // default 'div'
                            options={flickityOptions_sub} // takes flickity options {}
                            disableImagesLoaded={false} // default false
                            reloadOnUpdate // default false
                            static // default false
                            >
                                {products?.images?.map((img1)=>(
                                        <div className="carousel1_sub-cell"
                                        >
                                            <img src={`https://gamysh.com/${img1?.destination}-big.webp`} className="img_sub" />
                                        </div>
                                    ))}
                                    
                            </Flickity>
                            }
                    </div>
                    <div className='sm:hidden my-2 bg-blue-600 w-full mb-8 hover:bg-blue-600 text-white font-bold  px-4 py-2.5 rounded-md z-50"'>
                                    <a href={`tel:+993${products.phone}`} className="w-full h-full flex justify-center items-center">{Lang1[lang].call}</a>
                    </div>
                    <div className="map_location bg-blue-100">
                    <MapSingle products={products} />
                    </div>
                    </div>
                        <div className="w-full flex flex-col mt-8">
                        {/* <hr/> */}
                        <h2 className='text-xl font-medium'>{Lang1[lang].description_single}</h2>
                        <p className="text-sm text-gray-500 mt-5 break-words">{products?.description}</p>
                        <h2 className='text-xl mt-5 font-medium'>{Lang1[lang].info_single}</h2>
                        {products?.specifications?.map((spec)=>(
                        <div className="w-full flex sm:flex-row justify-between mt-5">
                            <div className="title_name1">
                                <p className='text-gray-500 font-medium font_size1' key={spec.id}>{spec?.name}</p>
                            </div>
                            <div className="filter_links1">
                            {spec?.values?.map((spec1)=>(
                                <p className='font_size1'>{spec1?.name}</p>
                            ))}
                            </div>
                        </div>
                        ))}
                        <div className='filter_links1 flex-col'>
                            <Button
                            color="red"
                            buttonType="link"
                            size="regular"
                            rounded={false}
                            block={false}
                            iconOnly={false}
                            ripple="dark"
                            type="submit"
                            className="sm:w-56 w-full my-5"
                            onClick={()=>{
                               setArz(true)
                            }}
                            >
                                {Lang1[lang].title_arz}
                            </Button>

                        </div>
                        {
                            arz ?
                            <div className='filter_more sm:px-0 px-4'>
                                <div className='filter_more1 rounded-lg'>
                                <div className="filter_header border-b flex items-center justify-between p-4 py-8">
                                <h1 className="text-xl">{Lang1[lang].title_arz}</h1>
                                <Icon size={22} icon={ic_close} 
                                onClick={()=>{
                                    setArz(false)
                                }} 
                            className="text-gray-400 text-medium cursor-pointer"/>
                                </div>

                                <div className="w-full flex flex-col justify-between mt-5 px-6">
                             <div className="title_name1">
                                 {/* <p className='text-gray-500 font-medium mb-2'>{Lang1[lang].title_arz}</p> */}
                             </div>
                         <form onSubmit={handleSubmit(onSubmit)} className="w-full flex-col">
                             <textarea {...register("message")} name="message" placeholder={Lang1[lang].title_arz}
                             className={errors.message?.message 
                             ? 
                             "border p-1 w-full h-44 text-sm px-2 focus:outline-none focus:ring-2 focus:ring-red-500 focus:ring-opacity-50"
                              : 
                             "border p-1 w-full h-44 text-sm px-2 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:ring-opacity-50"} ></textarea>
                             {/* <p className="text-red-500 my-1">{errors.descriptions?.message ? errors.descriptions.message : null}</p> */}
                             <p className="text-red-500 my-1">{errors.message?.message ? errors.message.message : null}</p>
                             <div className='w-full flex justify-end'>
                             <Button
                             color="lightBlue"
                             buttonType="link"
                             size="regular"
                             rounded={false}
                             block={false}
                             iconOnly={false}
                             ripple="dark"
                             type="submit"
                             className="w-32 mt-6"
                             >
                                 {Lang1[lang].des_send}
                             </Button>
                             </div>
                         </form>
                             </div>

                                </div>
                            </div>
                        
                        :
                        null
                        }

                        </div>
                    </>
                    }
                    </div>
                </div>

                
                
                <Footer />
        </main>

    )
}

const schema =  (lang) => Yup.object().shape({

    message:Yup.string().min(10, Lang1[lang].description_min).max(150, Lang1[lang].description_max1).required(Lang1[lang].required),
    
  });

export default Single;